/* Colours */
/* Fonts */
body {
  font-family: Kameron serif;
  color: #292E37;
  background: #CDCCCB;
  font-size: 18px; }

.Container {
  position: fixed;
  top: 50px;
  left: 50px;
  bottom: 0;
  right: 0;
  padding: 25px; }

.Row {
  margin: 10px 5px; }

nav ul, nav li {
  outline: 0;
  margin: 0;
  padding: 0; }

.NavTop {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background-color: #292E37; }

.NavTop__list {
  list-style-type: none;
  margin: 0;
  padding: 0; }

.NavTop__item {
  float: left;
  border-right: 1px solid #3B424F; }
  .NavTop__item :last-child {
    border-right: none; }

.NavTop__item--right {
  float: right; }

.NavTop__link {
  font-size: 20px;
  height: 36px;
  font-variant: small-caps;
  color: #CDCCCB;
  display: block;
  text-align: center;
  padding: 0 16px;
  padding-top: 14px;
  text-decoration: none;
  transition: color 0.5s ease; }
  .NavTop__link:hover {
    color: #4C8AAB; }

.NavTop__link--active {
  color: #4C8AAB;
  background-color: #CDCCCB; }

.NavTop__link--logo {
  width: 200px;
  font-weight: bold;
  padding: 12px 16px;
  font-size: 1.2em; }

.NavSide {
  background-color: #3B424F;
  position: fixed;
  top: 50px;
  bottom: 0;
  height: 100%;
  left: 0;
  width: 50px;
  overflow: hidden;
  -webkit-transition: width 0.1s linear;
  transition: width 0.1s linear;
  z-index: 99;
  /* Widen nav on hover */ }
  .NavSide:hover {
    width: 230px;
    overflow: visible; }

.NavSide__list {
  margin: 7px 0; }

.NavSide__item {
  position: relative;
  display: block;
  width: 250px; }

.NavSide__link {
  position: relative;
  display: table;
  border-collapse: collapse;
  border-spacing: 0;
  color: #CDCCCB;
  text-decoration: none;
  font-variant: small-caps;
  font-size: 20px;
  -webkit-transition: color .3s linear;
  transition: all .3s linear; }
  .NavSide__link:hover {
    color: #0A97DC; }

.NavSide__link--active {
  background-color: #CDCCCB;
  color: #292E37; }
  .NavSide__link--active:hover {
    color: #292E37; }

.NavSide__image {
  position: relative;
  display: table-cell;
  width: 50px;
  height: 50px;
  text-align: center;
  vertical-align: middle;
  font-size: 32px; }

.NavSide__text {
  position: relative;
  display: table-cell;
  vertical-align: middle;
  width: 190px; }

.Alert {
  padding: 15px 10px;
  font-weight: bold;
  border: 1px solid; }

.Alert--success {
  color: #2ECC40;
  border-color: #2ECC40;
  background: #D3FC9F; }

.Alert--error {
  color: #9c0033;
  border-color: #d73131;
  background: #9c0033; }

.Alert--warning {
  color: #ffff00;
  border-color: #8a6d3b;
  background: #ffff00; }
